import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import StrengthContents from "./StrengthContents";
import "../../styles/skills.css";

//  import
import htmlImg from "../../assets/skill_img/html.png";
import cssImg from "../../assets/skill_img/css.png";
import javascriptImg from "../../assets/skill_img/javascript.png";
import reactImg from "../../assets/skill_img/react.png";
import nextImg from "../../assets/skill_img/next.png";
import gsapImg from "../../assets/skill_img/gsap.png";
import gitImg from "../../assets/skill_img/git.png";
import githubImg from "../../assets/skill_img/github.png";
import sourcetreeImg from "../../assets/skill_img/sourcetree.png";
import notionImg from "../../assets/skill_img/notion_2.png";
import figmaImg from "../../assets/skill_img/figma_2.png";
import photoshopImg from "../../assets/skill_img/photoshop.png";
import illustratorImg from "../../assets/skill_img/illustrator.png";
import cafe24Img from "../../assets/skill_img/cafe24.png";

gsap.registerPlugin(ScrollTrigger);

const StrengthContentsBox = ({
  activeSkill,
}: {
  activeSkill: string | null;
}) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const cardsRef = useRef<(HTMLDivElement | null)[]>([]);

  const skillData = [
    {
      contents:
        "         HTML     .",
      skill: "HTML",
      img: htmlImg,
    },
    {
      contents:
        "    Javascript        .",
      skill: "CSS",
      img: cssImg,
    },
    {
      contents:
        "    , ,        .",
      skill: "Javascript",
      img: javascriptImg,
    },
    {
      contents:
        "React       UI , useState, useEffect        .",
      skill: "React",
      img: reactImg,
    },
    {
      contents:
        "Next.js        .",
      skill: "Next.js",
      img: nextImg,
    },
    {
      contents:
        " ,       GSAP   .",
      skill: "Gsap.js",
      img: gsapImg,
    },

    {
      contents:
        "Git Github       .",
      skill: "Git",
      img: gitImg,
    },
    {
      contents:
        "Git Github       .",
      skill: "Github",
      img: githubImg,
    },
    {
      contents:
        "SourceTree  Git GUI       .",
      skill: "SourceTree",
      img: sourcetreeImg,
    },
    {
      contents:
        "          .",
      skill: "Notion",
      img: notionImg,
    },
    {
      contents:
        "  ,     .",
      skill: "Figma",
      img: figmaImg,
    },
    {
      contents:
        " , , , SNS        .",
      skill: "Photoshop",
      img: photoshopImg,
    },
    {
      contents:
        "  ,          .",
      skill: "Illustrator",
      img: illustratorImg,
    },
    {
      contents:
        "Cafe24      ,      .",
      skill: "Cafe24",
      img: cafe24Img,
    },
  ];

  useEffect(() => {
    if (!containerRef.current) return;

    //   
    const validCards = cardsRef.current.filter(Boolean);
    if (validCards.length > 0) {
      gsap.set(validCards, {
        rotationY: 60,
        opacity: 0,
        scale: 0.9,
        y: 80,
        z: -100,
      });
    }

    //      
    cardsRef.current.forEach((card, index) => {
      if (card) {
        //  3D 
        gsap.to(card, {
          rotationY: 0,
          opacity: 1,
          scale: 1,
          y: 0,
          z: 0,
          duration: 1.5,
          ease: "power3.out",
          scrollTrigger: {
            trigger: card,
            start: "top 85%",
            end: "top 15%",
            scrub: 1.2,
            toggleActions: "play none none reverse",
          },
        });

        //   
        gsap.to(card, {
          rotationX: 2,
          duration: 0.8,
          ease: "power2.out",
          scrollTrigger: {
            trigger: card,
            start: "top 75%",
            end: "top 25%",
            scrub: 1,
            toggleActions: "play none none reverse",
          },
        });
      }
    });

    //   
    gsap.fromTo(
      containerRef.current,
      {
        y: 30,
        opacity: 0,
        scale: 0.95,
      },
      {
        y: 0,
        opacity: 1,
        scale: 1,
        duration: 2,
        ease: "power3.out",
        scrollTrigger: {
          trigger: containerRef.current,
          start: "top 85%",
          end: "top 20%",
          scrub: 1.5,
          toggleActions: "play none none reverse",
        },
      }
    );

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  useEffect(() => {
    if (!activeSkill) return;

    //     
    const activeIndex = skillData.findIndex(
      (skill) => skill.skill === activeSkill
    );
    if (activeIndex !== -1 && cardsRef.current[activeIndex]) {
      gsap.to(cardsRef.current[activeIndex], {
        scale: 1.05,
        rotationY: 5,
        duration: 0.3,
        ease: "power2.out",
      });

      //    
      cardsRef.current.forEach((card, index) => {
        if (index !== activeIndex && card) {
          gsap.to(card, {
            scale: 0.95,
            opacity: 0.7,
            duration: 0.3,
            ease: "power2.out",
          });
        }
      });
    } else {
      //    
      gsap.to(cardsRef.current, {
        scale: 1,
        rotationY: 0,
        opacity: 1,
        duration: 0.3,
        ease: "power2.out",
      });
    }
  }, [activeSkill]);

  return (
    <div ref={containerRef} className="strength_content_container">
      {skillData.map((skill, index) => (
        <div
          key={skill.skill}
          ref={(el) => (cardsRef.current[index] = el)}
          className="strength_card_wrapper"
        >
          <StrengthContents
            contents={skill.contents}
            skill={skill.skill}
            img={skill.img}
          />
        </div>
      ))}
    </div>
  );
};

export default StrengthContentsBox;
