import React, { useRef, useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "../styles/project.css";

const Project = () => {
  const sectionRef = useRef<HTMLElement>(null);
  const titleRef = useRef<HTMLHeadingElement>(null);
  const cardsRef = useRef<(HTMLDivElement | null)[]>([]);
  const [isMobile, setIsMobile] = React.useState(false);

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    //    (   )
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 1200);
    };

    checkMobile();
    window.addEventListener("resize", checkMobile);

    return () => {
      window.removeEventListener("resize", checkMobile);
    };
  }, []);

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    //  
    if (titleRef.current) {
      gsap.fromTo(
        titleRef.current,
        { opacity: 0, y: 50 },
        {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: "power2.out",
          scrollTrigger: {
            trigger: titleRef.current,
            start: "top 100%", // Skills     
            end: "bottom 0%",
            toggleActions: "play none none reverse",
          },
        }
      );
    }

    //  
    cardsRef.current.forEach((card, index) => {
      if (card) {
        gsap.fromTo(
          card,
          {
            opacity: 0,
            y: 100,
            scale: 0.8,
          },
          {
            opacity: 1,
            y: 0,
            scale: 1,
            duration: 0.8,
            ease: "back.out(1.7)",
            delay: index * 0.2,
            scrollTrigger: {
              trigger: card,
              start: "top 100%", // Skills     
              end: "bottom 0%",
              toggleActions: "play none none reverse",
            },
          }
        );
      }
    });

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  const work_categories = [
    {
      id: 1,
      title: "01 Professional Projects",
      projects: ["Herzion Shop", "ReportingX.", " "],
      bgColor: "#ffffff",
      targetSection: "professional",
    },
    {
      id: 2,
      title: "02 Learning Projects",
      projects: [
        "Groundplace",
        "IMELE",
        "SAINT LAURENT",
        "Waveyy",
        "ToDoList",
        "Apple",
      ],
      bgColor: "#ffffff",
      targetSection: "portfolio",
    },
    {
      id: 3,
      title: "03 Web Design",
      projects: [
        "Addmoa  ",
        "  ",
        "LH   ",
        "   ",
        "  ",
        "   ",
        "   ",
        "   ",
        "   ",
      ],
      bgColor: "#ffffff",
      targetSection: "web_design",
    },
  ];

  //   
  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  };

  //    
  const scrollToSpecificProject = (projectName: string) => {
    // portfolio   
    const portfolioSection = document.getElementById("portfolio");
    if (portfolioSection) {
      portfolioSection.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });

      //    GSAP ScrollTrigger    
      setTimeout(() => {
        if ((window as any).scrollToPortfolioProject) {
          (window as any).scrollToPortfolioProject(projectName);
        }
      }, 800); //       
    }
  };

  return (
    <section ref={sectionRef} className="work_section">
      <div className="work_container">
        <h1 ref={titleRef} className="work_title">
          Project.
        </h1>

        {isMobile ? (
          <Swiper
            modules={[Pagination]}
            spaceBetween={20}
            slidesPerView={1}
            navigation={false}
            pagination={{ clickable: true }}
            className="work_swiper"
          >
            {work_categories.map((category, index) => (
              <SwiperSlide key={category.id}>
                <div
                  ref={(el) => (cardsRef.current[index] = el)}
                  className="work_card"
                  style={
                    {
                      backgroundColor: category.bgColor,
                    } as React.CSSProperties
                  }
                  onClick={() => {
                    if (category.id === 2) {
                      // 02 Learning Projects      
                      scrollToSpecificProject(category.projects[0]);
                    } else {
                      //    
                      scrollToSection(category.targetSection);
                    }
                  }}
                >
                  <h2 className="work_card_title">
                    <span className="work_card_number">
                      {category.title.split(" ")[0]}
                    </span>
                    <span className="work_card_text">
                      {category.title.split(" ").slice(1).join(" ")}
                    </span>
                  </h2>
                  <ul className="work_project_list">
                    {category.projects.map((project, projectIndex) => (
                      <li
                        key={projectIndex}
                        className="work_project_item"
                        onClick={(e) => {
                          e.stopPropagation(); //    
                          if (category.id === 2) {
                            // Learning Projects     
                            scrollToSpecificProject(project);
                          } else {
                            //     
                            scrollToSection(category.targetSection);
                          }
                        }}
                      >
                        {project}
                      </li>
                    ))}
                  </ul>
                  <div className="work_cursor_icon">
                    <img src={`${process.env.PUBLIC_URL}/hover_icn.svg`} alt="hover cursor" />
                    <span className="work_cursor_text">click!</span>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        ) : (
          <div className="work_grid">
            {work_categories.map((category, index) => (
              <div
                key={category.id}
                ref={(el) => (cardsRef.current[index] = el)}
                className="work_card"
                style={
                  {
                    backgroundColor: category.bgColor,
                  } as React.CSSProperties
                }
                onClick={() => {
                  if (category.id === 2) {
                    // 02 Learning Projects      
                    scrollToSpecificProject(category.projects[0]);
                  } else {
                    //    
                    scrollToSection(category.targetSection);
                  }
                }}
              >
                <h2 className="work_card_title">
                  <span className="work_card_number">
                    {category.title.split(" ")[0]}
                  </span>
                  <span className="work_card_text">
                    {category.title.split(" ").slice(1).join(" ")}
                  </span>
                </h2>
                <ul className="work_project_list">
                  {category.projects.map((project, projectIndex) => (
                    <li
                      key={projectIndex}
                      className="work_project_item"
                      onClick={(e) => {
                        e.stopPropagation(); //    
                        if (category.id === 2) {
                          // Learning Projects     
                          scrollToSpecificProject(project);
                        } else {
                          //     
                          scrollToSection(category.targetSection);
                        }
                      }}
                    >
                      {project}
                    </li>
                  ))}
                </ul>
                <div className="work_cursor_icon">
                  <img src={`${process.env.PUBLIC_URL}/hover_icn.svg`} alt="hover cursor" />
                  <span className="work_cursor_text">click!</span>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </section>
  );
};

export default Project;
